  -moz-appearance: none;
  appearance: none;
  background: #d3d8df;
  border: 0;
  border-radius: .4rem;
  color: #21b8f7;
  height: .4rem;
  position: relative;
  width: 100%;
}

.progress::-webkit-progress-bar {
  background: transparent;
  border-radius: .4rem;
}

.progress::-webkit-progress-value {
  background: #21b8f7;
  border-radius: .4rem;
}

.progress::-moz-progress-bar {
  background: #21b8f7;
  border-radius: .4rem;
}

.progress:indeterminate {
  animation: progress-indeterminate 1.5s linear infinite;
  background: #d3d8df linear-gradient(to right, #21b8f7 30%, #d3d8df 30%) top left/150% 150% no-repeat;
}

.progress:indeterminate::-moz-progress-bar {
  background: transparent;
}

@keyframes progress-indeterminate {
  0% {
    background-position: 200% 0;
  }
  100% {
    background-position: -200% 0;
  }
}

.slider {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  background: transparent;
  display: block;
  height: 2.4rem;
  outline: none; 
  width: 100%;
}

.slider.tooltip:not([data-tooltip])::after {
  content: attr(value);
}

.slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  background: #21b8f7;
  border: 0;
  border-radius: 50%;
  height: 1.2rem;
  margin-top: -.55rem;
  transition: transform .2s;
  width: 1.2rem;
}

.slider::-moz-range-thumb {
  background: #21b8f7;
  border: 0;
  border-radius: 50%;
  height: 1.2rem;
  transition: transform .2s;
  width: 1.2rem;
}

.slider::-ms-thumb {
  background: #21b8f7;
  border: 0;
  border-radius: 50%;
  height: 1.2rem;
  transition: transform .2s;
  width: 1.2rem;
}

.slider:active::-webkit-slider-thumb {
  transform: scale(1.25);
}

.slider:active::-moz-range-thumb {
  transform: scale(1.25);
}

.slider:active::-ms-thumb {
  transform: scale(1.25);
}

.slider:disabled::-webkit-slider-thumb,
.slider.disabled::-webkit-slider-thumb {
  background: #e1e0e0;
  transform: scale(1);
}

.slider:disabled::-moz-range-thumb,
.slider.disabled::-moz-range-thumb {
  background: #e1e0e0;
  transform: scale(1);
}

.slider:disabled::-ms-thumb,
.slider.disabled::-ms-thumb {
  background: #e1e0e0;
  transform: scale(1);
}

.slider::-webkit-slider-runnable-track {
  background: #d3d8df;
  border-radius: .4rem;
  height: .1rem;
  width: 100%;
}

.slider::-moz-range-track {
  background: #d3d8df;
  border-radius: .4rem;
  height: .1rem;
  width: 100%;
}

.slider::-ms-track {
  background: #d3d8df;
  border-radius: .4rem;
  height: .1rem;
  width: 100%;
}

.slider::-ms-fill-lower {
  background: #21b8f7;
}

.timeline .timeline-item {
  display: flex;
  display: -ms-flexbox;
  margin-bottom: 2.4rem;
  position: relative;
}

.timeline .timeline-item::before {
  background: #556172;
  content: "";
  height: 100%;
  left: 11px;
  position: absolute;
  top: 2.4rem;
  width: 2px;
}

.timeline .timeline-item .timeline-left {
  -ms-flex: 0 0 auto;
  flex: 0 0 auto;
}

.timeline .timeline-item .timeline-content {
  -ms-flex: 1 1 auto;
  flex: 1 1 auto;
  padding: 2px 0 2px 3.2rem;
}

.timeline .timeline-item .timeline-icon {
  align-items: center;
  border-radius: 50%;
  color: #fff;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
  -ms-flex-pack: center;
  height: 2.4rem;
  justify-content: center;
  text-align: center;
  width: 2.4rem;
}

.timeline .timeline-item .timeline-icon::before {
  border: .1rem solid #21b8f7;
  border-radius: 50%;
  content: "";
  display: block;
  height: .8rem;
  left: .8rem;
  position: absolute;
  top: .8rem;
  width: .8rem;
}

.timeline .timeline-item .timeline-icon.icon-lg {
  background: #21b8f7;
  line-height: 1.625rem;
}

.timeline .timeline-item .timeline-icon.icon-lg::before {
  content: none;
}

